export default {
	data() {
		return {
			PageCur: 'home'
		}
	},
	onShow: function() {
		this.PageCur = 'home'
	},
	onPullDownRefresh() {
		console.log('refresh');
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
